<template>
  <div class="search-detail">
    <vs-row>
      <vs-col vs-type="flex" vs-w="6" style="padding-top:1em">
        <vs-breadcrumb>
          <li>
            <a href="/" title="Home" v-colorChange>Home</a>
            <span class="vs-breadcrum--separator">/</span>
          </li>
          <li aria-current="page" class="active">Search Result:</li>
          <li aria-current="page" class="search-result-value" v-colorChange>" {{searchValue}} "</li>
        </vs-breadcrumb>
      </vs-col>
      <vs-col vs-type="flex" vs-w="6" style="padding-top:1em;padding-right:1em">
        <Search />
      </vs-col>
      <div v-if="searchItem.length > 0" style="width:100%">
        <vs-col
          type="flex"
          vs-justify="center"
          v-for="item in searchItem"
          vs-align="center"
          vs-w="3"
          style="padding:1em;width:20%"
          :key="item.poster_path"
        >
          <router-link :to="{ name: 'movie', params: { id: item.id }}">
            <vs-card class="cardx">
              <div slot="media">
                <img :src="item.poster_path | setImagePath" />
              </div>
              <div>
                <span>{{item.overview}}</span>
              </div>
              <div slot="footer">
                <vs-row vs-justify="flex-end">
                  <vs-button type="gradient" color="danger" icon="favorite"></vs-button>
                  <vs-button color="primary" icon="turned_in_not"></vs-button>
                  <vs-button color="rgb(230,230,230)" color-text="rgb(50,50,50)" icon="settings"></vs-button>
                </vs-row>
              </div>
            </vs-card>
          </router-link>
        </vs-col>
      </div>
      <div v-else style="width:100%;display: flex;justify-content: center;">
        <vs-col type="flex" vs-justify="center" vs-align="center" vs-w="3" style="padding:1em">
          <vs-alert active="true">Data not found :(</vs-alert>
        </vs-col>
      </div>
      <Pagination :page="page" :totalPage="totalPage" @more="more" @prev="prev" />
    </vs-row>
  </div>
</template>


<script>
import { mapGetters } from "vuex";
const Search = () => import("@/views/Search/Index.vue");
const Pagination = () => import("@/components/pagination.vue");

export default {
  head: {
    title: {
      inner: "Search Result"
    }
  },
  computed: {
    ...mapGetters({
      searchItem: "search/getResult",
      searchValue: "search/getSearchValue",
      page: "search/getCurrentPage",
      totalPage: "search/getTotalPage"
    })
  },
  mounted() {
    this.initMovieDetail();
  },
  methods: {
    initMovieDetail() {
      let value = this.$route.params.q;
      if (value.length > 0) {
        this.$store.dispatch("search/fetchSearch", { value });
      }
    },
    more() {
      let value = this.$route.params.q;
      this.$store.dispatch("search/fetchSearchMore", { value });
    },
    prev() {
      let value = this.$route.params.q;
      this.$store.dispatch("search/fetchSearchPrev", { value });
    }
  },
  components: { Search, Pagination },
  watch: {
    $route() {
      this.initMovieDetail();
    }
  }
};
</script>


<style lang="scss">
.search-detail {
  height: 100%;
  .search-result-value {
    font-weight: bold;
    margin-left: 5px;
  }
  .vs-card--header {
    overflow: hidden;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .vs-card--content {
    height: 100px;
    overflow: hidden;
    text-align: left;
    font-weight: 300;
  }
  .cardx {
    cursor: pointer;
    &:hover {
      -webkit-box-shadow: 0 4px 25px 0 rgba(0, 0, 0, 0.3);
      box-shadow: 0 4px 25px 0 rgba(0, 0, 0, 0.3);
    }
  }
}
</style>